import SearchResult from '@/app/components/SearchResults';
import searchLoader from '@/app/utils/searchLoader';
import React from 'react';
import { use } from 'react';

export default async function SearchResultsPage({ params }: { params: { searchQuery: string } }) {
  let searchResult = await searchLoader(params.searchQuery);

  return (
    <div>
      <h1>Search Results for "{params.searchQuery}"</h1>
      {searchResult.map((drink) => () => <SearchResult drinks={drink} />)}
    </div>
  );
}